const main = () => import('@/views/main/index.vue') // 
const withdrawalMain = () => import('@/views/main/withdrawal/main.vue') // 
const withdrawalDetail = () => import('@/views/main/withdrawal/detail.vue') // 
const withdrawal = () => import('@/views/main/withdrawal/index.vue') // 
const withdrawalHelp = () => import('@/views/main/withdrawal/help.vue') // 
const withdrawalResult = () => import('@/views/main/withdrawal/result.vue') // 

export default [{
    path: '/main',
    name: 'mainPage',
    meta: {
      title: '',
      showBottomTab: true,
      cacheTos: ['KTVDetail', 'cardTicket', 'OrderSharing', 'personalCenter']
    },
    component: main
  }, {
    path: '/withdrawalMain',
    name: 'withdrawalMain',
    component: withdrawalMain,
    meta: {
      title: '',
    },
  },
  {
    path: '/withdrawalDetail',
    name: 'withdrawalDetail',
    component: withdrawalDetail,
    meta: {
      title: '',
    },
  },
  {
    path: '/withdrawal',
    name: 'withdrawal',
    component: withdrawal,
    meta: {
      title: '',
    },
  },
  {
    path: '/withdrawalhelp',
    name: 'withdrawalhelp',
    component: withdrawalHelp,
    meta: {
      title: '',
    },
  },
  {
    path: '/withdrawalResult',
    name: 'withdrawalResult',
    component: withdrawalResult,
    meta: {
      title: '',
    },
  },
]
