const ktvmanage = () => import('@/views/ktv/index.vue') // KTV
const ktvDetail = () => import('@/views/ktv/detail.vue') //KTV

/*  */
const contract = () => import('@/views/ktv/components/contract') // 
const contractDetail = () => import('@/views/ktv/components/contract/detail') // 
const contractChangeHistory = () => import('@/views/ktv/components/contract/history') // 
const approvalRecord = () => import('@/views/ktv/components/contract/approvalRecord') // 

/*  */
const implementation = () => import('@/views/ktv/components/implementation') // 
const impletDetail = () => import('@/views/ktv/components/implementation/detail') // 
const boxInformation = () => import('@/views/ktv/components/implementation/components/boxInformation') // 
const QRCode = () => import('@/views/ktv/components/implementation/components/QRCode') // 
const formatResolution = () => import('@/views/ktv/components/implementation/components/formatResolution') // 

/*  */
const account = () => import('@/views/ktv/components/accountInfo') // 

/*  */
const accountNumber = () => import('@/views/ktv/components/accountNumber') // 

/*  */
const activity = () => import('@/views/ktv/components/activity') // 

export default [{
		path: '/ktvmanage',
		name: 'ktvmanage',
		meta: {
			title: 'KTV',
			showBottomTab: true,
			cacheTos: ['mainPage', 'KTVDetail', 'cardTicket', 'OrderSharing', 'personalCenter', 'searchPage']
		},
		component: ktvmanage
	},
	{
		path: '/KTVDetail',
		name: 'KTVDetail',
		meta: {
			title: '',
			cacheTos: ['contract', 'implementation', 'account', 'accountNumber', 'activity']
		},
		component: ktvDetail
	},
	{
		path: '/contract',
		name: 'contract',
		meta: {
			title: '',
			cacheTos: ['contractDetail']
		},
		component: contract
	},
	{
		path: '/contractDetail',
		name: 'contractDetail',
		meta: {
			title: '',
			cacheTos: ['contractChangeHistory', 'approvalRecord', 'PdfPreview']
		},
		component: contractDetail
	},
	{
		path: '/contractChangeHistory',
		name: 'contractChangeHistory',
		meta: {
			title: ''
		},
		component: contractChangeHistory
	},
	{
		path: '/approvalRecord',
		name: 'approvalRecord',
		meta: {
			title: ''
		},
		component: approvalRecord
	},
	{
		path: '/implementation',
		name: 'implementation',
		meta: {
			title: ''
		},
		component: implementation
	},
	{
		path: '/impletDetail',
		name: 'impletDetail',
		meta: {
			title: ''
		},
		component: impletDetail
	},
	{
		path: '/boxInformation',
		name: 'boxInformation',
		meta: {
			title: ''
		},
		component: boxInformation
	},
	{
		path: '/QRCode',
		name: 'QRCode',
		meta: {
			title: ''
		},
		component: QRCode
	},
	{
		path: '/formatResolution',
		name: 'formatResolution',
		meta: {
			title: ''
		},
		component: formatResolution
	},
	{
		path: '/account',
		name: 'account',
		meta: {
			title: ''
		},
		component: account
	},
	{
		path: '/accountNumber',
		name: 'accountNumber',
		meta: {
			title: ''
		},
		component: accountNumber
	},
	{
		path: '/activity',
		name: 'activity',
		meta: {
			title: ''
		},
		component: activity
	},
]