// ktv
export const getContractList = (params) => {
	return new Promise((resolve, reject) => {
		axios.request({
		  url: `/ktv/place/contract`,
		  params,
		  method: 'get'
		}).then(res => {
			let priorityArr = [2, 1, 3, 4]; // 
			res.data.results.sort((a, b) => {
				if(priorityArr.indexOf(a.state) > priorityArr.indexOf(b.state)){
					return 1;
				}else{
					return -1;
				}
			})
			let results = {
				total: res.data.count,
				data: res.data.results
			}
			resolve(results)
		}).catch(err => {
			reject(err)
		})
	})
}

/* 
 * 
 * @params{Number} type , 1, 2
 * @params{Number} ID id
 */
export const getContractDetail = (type, ID) => {
	return new Promise((resolve, reject) => {
		let ajax = null;
		if(type == 1){
			ajax = getQKcontractDetail;
		}else{
			ajax = getZHcontractDetail;
		}
		ajax(ID).then(res => {
			resolve(res)
		}).catch(err => {
			reject(err)
		})
	})
}


