export const syncBoxInfoList = (data) => {
	return new Promise((reslove, reject) => {
		putImplementation(data).then(res => {
			reslove(res)
		}).catch(err => {
			reject(err)
		})
	})
}

// ktv
const putImplementation = (data) => {
	let id = data.id;
	delete data.id;
	return axios.request({
	  url: `/ktv/place/implement/${id}`,
	  data,
	  method: 'patch'
	})
}

