import axios from '@/libs/api.request'

// 
export const login = ({
  username,
  password
}) => {
  const data = {
    username: username,
    password: password
  }
  return axios.request({
    url: '/cperm/token/get',
    data,
    method: 'post'
  })
}

// ()
export const getUserType = (params) => {
  return axios.request({
    url: `/cperm/users/user_type`,
    params,
    method: 'get',
  })
}

