const personalCenter = () => import('@/views/personalCenter') // 
const personalInfo = () => import('@/views/personalCenter/info') // 
const personContract = () => import('@/views/personalCenter/contract') // 
const changePassword = () => import('@/views/login/components/forgetPassword/components/changePassword') // 
const PdfPreview = () => import('@/components/PdfPreview') // pdf

export default [{
    path: '/personalCenter',
    name: 'personalCenter',
    meta: {
      title: '',
      showBottomTab: true,
      cacheTos: ['personalInfo', 'personContract', 'withdrawalDetail', 'withdrawal', 'withdrawalMain'],
    },
    component: personalCenter
  },
  {

    path: '/personalInfo',
    name: 'personalInfo',
    meta: {
      title: '',
      cacheTos: ['personalCenter'],
    },
    component: personalInfo
  },
  {
    path: '/personContract',
    name: 'personContract',
    meta: {
      title: '',
      cacheTos: ['contractDetail', 'personalCenter']
    },
    component: personContract
  }, {
    path: '/changePassword',
    name: 'changePassword',
    meta: {
      title: '',
      cacheTos: ['personalCenter']
    },
    component: changePassword
  },
  {
    path: '/PdfPreview',
    name: 'PdfPreview',
    meta: {
      title: 'PDF'
    },
    component: PdfPreview
  }
]
