import {
  socket
}
from "@/api/information";
import config from "@/config";
import Centrifuge from "centrifuge";
let socketIns = null;
export default {
  getWebSocket(userID, getUnRead) {
    if ("WebSocket" in window && !self.connectStatus) {
      const params = {
        user_id: userID
      };
      socket(params).then(res => {
        const sign = res.data.access_token; //  secretusertimestamphashmapsha256
        const timestamp = res.data.timestamp; // 
        const user = res.data.user; // id
        if (!user) return
        socketIns = new Centrifuge({
          url: config.baseUrl.socket,
          user: user,
          timestamp: timestamp,
          token: sign
        });
        // Websocket,
        socketIns.connect();
        // msg:userid 
        socketIns.subscribe(
          // `msg:${self.$store.state.user.userID}`,
          `msg:${userID}`,
          message => {
            /**
             * userID  
             * @message {object}: 
             */
            console.log("msg");
            console.log("message", message);
            getUnRead();
          }
        );

        socketIns.on("connect", context => {
          // console.log("", context);
        });

        socketIns.on("disconnect", context => {
          // console.log("", context);
        });

        socketIns.on("error", error => {
          // console.log("", error);
        });

        // websocketserver
        window.onbeforeunload = () => {
          socketIns.disconnect();
        };
      });
      return socketIns
    }
    return socketIns
  }
}
