const cardTicket = () => import('@/views/cardTicket') // 
const cardTicketRecord = () => import('@/views/cardTicket/record')  // 


export default [
	{
		path: '/cardTicket',
		name: 'cardTicket',
		meta:{
			title: '',
			showBottomTab: true
		},
		component: cardTicket
	},
	{
		path: '/cardTicketRecord',
		name: 'cardTicketRecord',
		meta:{
			title: ''
		},
		component: cardTicketRecord
	},
]